<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Delivery History</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mx-0 my-0"
          @click="$router.push('/add-new-delivery')"
          type="primary"
          v-ripple.400
        >
          Add Delivery
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3 mt-sm-0 pt-0">
      <div class="box pt-3">
        <div class="row">
          <!-- <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div> -->
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-input
                v-model="inputHelper"
                @change="setFilterQuery"
                class="table-search mr-2"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>

              <el-date-picker
                class="mr-2 filter-input"
                v-model="filters.date"
                type="date"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>
        <div class="w-100 el-main-table">
          <el-table
            ref="multipleTable"
            :data="deliveryHistories.results"
            style="width: 100%"
          >
            <el-table-column type="index" width="30"> </el-table-column>

            <el-table-column label="Image" width="220">
              <template slot-scope="scope">
                <img
                  @click="openLightBox(scope.row.image)"
                  :src="scope.row.image"
                  style="
                    width: 210px;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 5px;
                    margin-right: 5px;
                  "
                  alt=""
                />
              </template>
            </el-table-column>

            <el-table-column label="Customer">
              <template slot-scope="scope">
                <span>{{ scope.row.customer_name }}</span>
              </template>
            </el-table-column>

            <el-table-column label="Order ID">
              <template slot-scope="scope">
                #{{ scope.row.order_id }}
              </template>
            </el-table-column>

            <el-table-column label="Delivery">
              <template slot-scope="scope">
                <span>{{ scope.row.delivery_name }}</span>
              </template>
            </el-table-column>

            <el-table-column label="Date">
              <template slot-scope="scope">
                <span>{{
                  $moment(scope.row.created_at).format("YYYY-MM-DD")
                }}</span>
              </template>
            </el-table-column>

            <el-table-column label="Delete">
              <template slot-scope="scope">
                <el-button
                  class="mx-0 my-0"
                  @click="deleteDelivery(scope.row.id)"
                  type="primary"
                  v-loading="deleting"
                  v-ripple.400
                >
                  Delete
                </el-button>
              </template>
            </el-table-column>

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-loading="loading">
            <p class="pt-3"></p>
            <!-- <el-pagination
              background
              @current-change="
                (index) => ((filters.page = index), getdeliveryHistories())
              "
              :current-page="parseInt(filters.page)"
              layout="prev, pager, next"
              :page-count="deliveryHistories.total_pages"
            >
            </el-pagination> -->
          </div>
        </div>
      </div>
    </div>

    <CoolLightBox
      :items="lightBoxFile"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      inputHelper: "",
      lightBoxFile: [],
      deleting: false,
      index: null,
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 50,
        page: 1,
        date: this.$moment().format("YYYY-MM-DD"),
      },
      loading: false,
      actions: [],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Customer A–Z",
          value: "customer__name",
        },
        {
          name: "Customer Z–A",
          value: "-customer__name",
        },
      ],
      loadingDelivey: false,
      deliveries: [],
      selectedDate: null,
      selectedDelivery: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      isReadyToGet: false,
      deliveryHistories: {
        next: 1,
        results: [],
        total_pages: 0,
      },
      scroll: 0,
    };
  },
  components: { empty },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  mounted() {
    this.filters = {
      query: this.$route.query.query || "",
      order_by: this.$route.query.order_by || "-updated_at",
      pageSize: this.$route.query.page_size || "50",
      page: this.$route.query.page || "1",
      date: this.$route.query.date || this.$moment().format("YYYY-MM-DD"),
    };
    (this.inputHelper = this.$route.query.query || ""),
      (this.isReadyToGet = true);
    this.getdeliveryHistories();
  },
  watch: {
    filters: {
      handler: function (value) {
        if (this.isReadyToGet) {
          this.$router
            .push({
              path: "/delivery-history",
              query: {
                query: value.query,
                page_size: value.pageSize,
                page: value.page,
                order_by: value.order_by,
                date: value.date,
              },
            })
            .catch(() => {});
        }
      },
      deep: true,
    },
  },
  methods: {
    handleScroll(e) {
      const scrollHeight =
        e.target.scrollingElement.scrollHeight -
        e.target.scrollingElement.clientHeight * 2;
      if (scrollHeight < e.target.scrollingElement.scrollTop) {
        if (!this.loading) {
          this.getdeliveryHistories();
        }
      }
    },
    openLightBox(image) {
      this.lightBoxFile = [image];
      this.index = 0;
    },
    updateStatus() {
      // if (com == "Delete") {
      //   this.deletedeliveryHistories();
      // }
    },
    deleteDelivery(id) {
      this.deleting = true;
      var url = `delivery-history/?id=` + id;
      this.$axios
        .delete(url)
        .then(() => {
          this.filters = {
            query: "",
            order_by: "-updated_at",
            pageSize: "50",
            page: "1",
            date: this.$moment().format("YYYY-MM-DD"),
          };
          this.deleting = false;
          this.$noti.display({
            message: "Success!",
            status: "success",
            color: "success",
          });
        })
        .catch((err) => {
          this.deleting = false;
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    deletedeliveryHistories() {
      this.loading = true;
      var data = [];
      this.selected.forEach((ele) => {
        data.push(ele.id);
      });
      var url = `deliveryHistories/delete/`;
      this.$axios
        .post(url, { data: data })
        .then(() => {
          this.getdeliveryHistories();
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    setFilterQuery() {
      this.filters.query = this.inputHelper;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },

    getdeliveryHistories() {
      if (this.deliveryHistories.next) {
        this.loading = true;
        this.gettingOrder = true;
        var url = `delivery-history-list/?date=${this.filters.date}&page=${this.deliveryHistories.next}&page_size=${this.filters.pageSize}&query=${this.filters.query}&order_by=${this.filters.order_by}`;
        this.$axios
          .get(url)
          .then((response) => {
            this.gettingOrder = false;
            const scroll = window.scrollY;
            this.deliveryHistories = {
              ...response.data,
              results: [
                ...this.deliveryHistories.results,
                ...response.data.results,
              ],
            };
            setTimeout(() => {
              window.scroll(0, scroll);
            }, 50);
            setTimeout(() => {
              this.loading = false;
            }, 100);

            this.$store.commit("setIsReady", true);
          })
          .catch((err) => {
            this.loading = false;
            this.gettingOrder = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
  },
};
</script>

